import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { MovieModel } from './models/movie-model';

interface RemoteMovieData {
  id: number;
  title: string;
  rating: number;
  votes: number;
  posterSrc: string;
  genres: string[];
  releaseDate: string;
}

@Injectable({
  providedIn: 'root'
})
export class MovieService {
  private data: Observable<RemoteMovieData[]>;

  private static map(obj: RemoteMovieData) {
    return new MovieModel(obj.id, obj.title, `Rating: ${obj.rating}`, obj.posterSrc);
  }

  constructor(http: HttpClient) {
    this.data = http.get('http://vps135320.ovh.net:1234/list/') as Observable<RemoteMovieData[]>;

    // this.data = [
    //   { id: 1, title: 'Matrix Reloaded', description: 'Matrix Reloaded Description'},
    //   { id: 2, title: 'Matrix 2', description: 'Matrix 2 Description'},
    //   { id: 3, title: 'Matrix Impossibru', description: 'Matrix Impossibru Description'},
    //   { id: 4, title: 'Kot w Butach', description: 'Kot w Butach Description'},
    //   { id: 5, title: '13 dzielnica', description: '13 dzielnica Description'},
    // ];
  }

  public async getLastMovies(limit = 3, offset = 0) {
    const data = await this.data.toPromise();
    return data ? data.slice(offset, limit).map(obj => MovieService.map(obj)) : [];
  }

  public async getMovie(id: number) {
    const data = await this.data.toPromise();
    if (data) {
      const obj: RemoteMovieData = data.find(m => m.id === id);
      if (obj) {
        return MovieService.map(obj);
      }
    }

    return null;
  }
}
