import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MovieListPageComponent } from './components/@init/movie-list-page.component';
import { MovieListDetailsPageComponent } from './components/movie-list-details-page/movie-list-details-page.component';
import { MovieResolver } from './movie.resolver';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    component: MovieListPageComponent,
  },
  {
    path: ':id',
    component: MovieListDetailsPageComponent,
    resolve: {
      movie: MovieResolver
    },
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: []
})
export class MoviePageRoutingModule {
}
