import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { MovieService } from '../shared-module/movie.service';
import { MovieModel } from '../shared-module/models/movie-model';

@Injectable()
export class MovieResolver implements Resolve<MovieModel> {
  constructor(private movieService: MovieService) {
    console.log('test');
  }

  resolve(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<any>|Promise<any>|any {
    return this.movieService.getMovie(parseInt(route.params.id, 10));
  }
}
