
export class MovieModel {
  // tslint:disable-next-line:variable-name
  constructor(private _id: number, private _title: string, private _description: string, private _posterSrc: string) {}


  get id(): number {
    return this._id;
  }

  get title(): string {
    return this._title;
  }

  get description(): string {
    return this._description;
  }

  get posterSrc(): string {
    return this._posterSrc;
  }
}
